<template>
  <div class="search">
    <div>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="单点查询" name="first">
          <el-select v-model="SingleValue" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <span class="inputValue">
            <el-input v-model="inputSingle" placeholder="请输入内容"></el-input>
          </span>
          <span class="connect">查询最大深度：{{sliderValue}}</span>
          <span class="slideValue">
            <el-slider v-model="sliderValue" :step="1" :max="5" :min="1" show-stops></el-slider>
          </span>
          <el-button class="btn" type="primary" plain @click="handelSearchSingle()">查询</el-button>
        </el-tab-pane>
        <el-tab-pane label="关系查询" name="second">
          <div class="searchCon">
            <el-select v-model="relationshipValueL" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
            <span class="inputValue">
              <el-input v-model="inputRelationshipValueL" placeholder="请输入内容"></el-input>
            </span>
            <el-select v-model="relationshipValueR" placeholder="请选择" style="margin-left:60px;">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
            <span class="inputValue">
              <el-input v-model="inputRelationshipValueR" placeholder="请输入内容"></el-input>
            </span>
            <el-button class="btn" type="primary" plain @click="handelSearchRelationship()">查询</el-button>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="visjs" v-loading="loading">
      <p v-show="isPlay" class="noData">未查询到该搜索对象！</p>
      <div id="visjs"></div>
    </div>
    <div class="export">
      <!-- <el-button type="primary" @click="handelImage()">导出图片</el-button> -->
      <p>*右键另存为图片</p>
    </div>
  </div>
</template>

<script>
import vis from "vis";
export default {
  name: "HelloWorld",
  data() {
    return {
      img: "",
      Person: require("./../assets/image/person.png"),
      Addr: require("./../assets/image/addr.png"),
      BankAcc: require("./../assets/image/bank.png"),
      Company: require("./../assets/image/company.png"),
      Device: require("./../assets/image/computer.png"),
      PRJ: require("./../assets/image/prj.png"),
      Tel: require("./../assets/image/tel.png"),
      Vehicel: require("./../assets/image/car.png"),
      carNum: require("./../assets/image/carnum.png"),
      default: require("./../assets/image/default.png"),
      activeName: "first",
      options: [
        {
          value: "Addr",
          label: "地址"
        },
        {
          value: "供应商银行账号",
          label: "银行卡号"
        },
        {
          value: "Company",
          label: "公司"
        },
        {
          value: "PRJ",
          label: "项目"
        },
        {
          value: "Person",
          label: "人"
        },
        {
          value: "Tel",
          label: "电话"
        },
        {
          value: "Vehicel",
          label: "VIN"
        },
        {
          value: "车牌号",
          label: "车牌号"
        }
      ],
      SingleValue: "",
      relationshipValueL: "",
      relationshipValueR: "",
      inputSingle: "",
      inputRelationshipValueL: "",
      inputRelationshipValueR: "",
      sliderValue: 5,
      loading: false,
      isPlay: false
    };
  },
  mounted() {
    const data = {
      type: "0"
    };
    this.getData(data);
  },
  methods: {
    handelSearchSingle() {
      if (this.SingleValue && this.inputSingle) {
        const data = {
          startNode: this.SingleValue,
          startValue: `'${this.inputSingle}'`,
          arra: `${this.sliderValue}`,
          type: "0"
        };
        console.log(data);
        this.getData(data);
      } else {
        this.open3();
      }
    },
    open3() {
      this.$message({
        message: "请输入要查询的内容！",
        type: "warning"
      });
    },
    getData(data) {
      const ajaxIp = this.$GLOBALCONFIG.webapi; //接口ip
      const groupNetwork = {
        type: "post",
        url: ajaxIp + "/sq/queryNodeRelation",
        data: data,
        id: "visjs"
      };
      this.getAjaxData(
        groupNetwork.type,
        groupNetwork.url,
        groupNetwork.data,
        groupNetwork.id
      );
    },
    handelSearchRelationship() {
      if (this.relationshipValueL && this.inputRelationshipValueL) {
        const data = {
          startNode: this.relationshipValueL,
          startValue: this.relationshipValueR,
          endNode: `'${this.inputRelationshipValueL}'`,
          endValue: `'${this.inputRelationshipValueR}'`,
          type: "1"
        };
        this.getData(data);
      } else {
        this.open3();
      }
    },
    handelImage() {
      // var canvas = document.getElementsByTagName("canvas")[0];
      // this.img=canvas.toDataURL("image/png");
    },
    visual(prjId, c_no) {
      var ajaxIp = "http://sq-dev:8090"; //接口ip
      var groupNetwork = {
        type: "post",
        url: ajaxIp + "/sq/queryNodeRelation",
        data: {
          prjId: prjId,
          c_no: c_no
        },
        id: "visjs"
      };
      this.getAjaxData(
        groupNetwork.type,
        groupNetwork.url,
        groupNetwork.data,
        groupNetwork.id
      );
    },
    getAjaxData(type, url, data, id) {
      const _this = this;
      this.loading = true;
      this.ajax({
        type: type,
        url: url,
        //加post的话用到data  "MATCH s=(n:Person)-[r:亲属]->(m) return s limit 5"
        //get的话直接用&拼接
        data: JSON.stringify(data),
        async: true,
        success: function(res) {
          if (res.nodes.length > 0) {
            _this.handleData(res, id);
            _this.isPlay = false;
          } else {
            const obj = {};
            _this.handleData(res, id);
            _this.isPlay = true;
          }
          console.log(_this.isPlay);
          _this.loading = false;
        }
      });
    },
    visData(nodes, edges, id) {
      // create a network
      var container = document.getElementById(id);
      container.style.width = "100%";
      container.style.height = 700 + "px";
      var data = {
        nodes: nodes,
        edges: edges
      };
      var options = {
        edges: {
          shadow: true, //连接线阴影配置
          smooth: true, //是否显示方向箭头
          arrows: { to: true } //箭头指向from节点
        },
        //设置节点的相互作用
        interaction: {
          //鼠标经过改变样式
          hover: true
          //设置禁止缩放
          //zoomView:false
        },
        //力导向图效果
        physics: {
          enabled: true,
          barnesHut: {
            gravitationalConstant: -4000,
            centralGravity: 0.3,
            springLength: 120,
            springConstant: 0.04,
            damping: 0.09,
            avoidOverlap: 0
          }
        },
        nodes: {
          shape: "dot",
          size: 30,
          color: {
            background: "#fff"
          },
          font: {
            size: 16
          },
          borderWidth: 1,
          shadow: false
        }
      };
      var network = new vis.Network(container, data, options);
      network.on("dragEnd", function(params) {
        if (params.nodes && params.nodes.length > 0) {
          network.clustering.updateClusteredNode(params.nodes[0], {
            physics: false
          });
        }
      });
      network.on("click", function(params) {
        console.log(
          "click event, getNodeAt returns: " +
            this.getNodeAt(params.pointer.DOM)
        );
      });
      network.on("stabilized", function(params) {});
    },
    handleData(res, id) {
      var objEdges = {
        color: { color: "rgba(30,30,30,0.6)", highlight: "red" }
      };
      var imageNode = {
        Person: this.Person,
        Addr: this.Addr,
        BankAcc: this.BankAcc,
        Company: this.Company,
        Device: this.Device,
        PRJ: this.PRJ,
        Tel: this.Tel,
        Vehicel: this.Vehicel,
        carNum: this.carNum,
        default: this.default
      };
      var nodes = res.nodes;
      var newNodes = [];
      var edges = res.edges;
      var newEdges = [];
      for (var i = 0; i < nodes.length; i++) {
        var label = "";
        var imageLable = "";
        var colorFont = "#000";
        if (nodes[i].properties && nodes[i].properties.name) {
          label = nodes[i].properties.name;
          if (label == this.inputSingle) {
            colorFont = "red";
          } else {
            colorFont = "#000";
          }
        } else {
          label = "label";
        }

        if (
          nodes[i].label[0] &&
          nodes[i].label[0] !== "车牌号" &&
          imageNode[nodes[i].label[0]]
        ) {
          imageLable = imageNode[nodes[i].label[0]];
        } else if (nodes[i].label[0] == "车牌号") {
          imageLable = imageNode.carNum;
        } else {
          imageLable = imageNode.default;
        }
        var objNodes = {
          shape: "circularImage",
          font: { color: colorFont }
        };
        newNodes.push({
          id: nodes[i].id,
          label: label,
          ...objNodes,
          image: imageLable
        });
      }
      for (var i = 0; i < edges.length; i++) {
        newEdges.push({
          from: edges[i].form,
          to: edges[i].to,
          label: edges[i].label,
          ...objEdges
        });
      }
      var nodes = new vis.DataSet(newNodes);
      var edges = new vis.DataSet(newEdges);
      console.log("edges", newEdges);
      console.log("newNodes", newNodes);
      this.visData(nodes, edges, id);
    },
    ajax(obj) {
      var xhr;
      if (window.XMLHttpRequest) {
        xhr = new XMLHttpRequest();
      } else {
        xhr = new ActiveXObject("Microsoft.XMLHTTP");
      }
      xhr.open(obj.type, obj.url, obj.async);

      if (obj.type.toLowerCase() == "get") {
        xhr.send(toUrl.data);
      } else if (obj.type.toLowerCase() == "post") {
        xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xhr.send(obj.data);
      }
      xhr.onreadystatechange = function() {
        if (xhr.readyState == 4 && xhr.status == 200) {
          var data = JSON.parse(xhr.responseText);
          obj.success(data);
        }
      };
      // get 请求
      function toUrl(obj) {
        var arr = [];
        for (var i in obj) {
          arr.push(i + "=" + obj[i]);
        }
        return arr.join("&");
      }
    },
    handleClick(tab, event) {
      console.log(tab, event);
    }
  }
};
</script>

